@forward 'globals';

.user-profile {
    padding: 2rem;
    border-radius: 20px;
    background-color: #EEE;
}

.profile-wrapper {
    margin-top: 2rem;
    position: relative;
    min-width: 20rem;

    &:first-of-type {
        margin-top: 3rem;
    }

    button {
        position: absolute;
        right: 0;
        top: 0;
        background: none;
        color: blue;
        padding: 0;
    }

    &:not(:last-child) {
        padding-bottom: 1rem;
        border-bottom: 1px solid #E6E6E6;
    }

    .profile-img {
        width: 5rem;
        height: 5rem;
        margin-bottom: 1rem;
    
        &, img {
            border-radius: 50%;
        }
    }

    .bio {
        margin-top: 1rem;
    }
}