* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  display: grid;
  place-content: center;
  min-height: 100vh;
}

img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

button, input {
  border: 1px solid #E6E6E6;
  border-radius: 10px;
  outline: none;
  padding: 0.5rem 1rem;
  font: inherit;
}

input {
  flex: 1;
}

button {
  background-color: #DDD;
  border-color: transparent;
  margin-left: -20px;
  cursor: pointer;
}

.flex {
  display: flex;
  gap: 2rem;
}

.user-profile {
  padding: 2rem;
  border-radius: 20px;
  background-color: #EEE;
}

.profile-wrapper {
  margin-top: 2rem;
  position: relative;
  min-width: 20rem;
}
.profile-wrapper:first-of-type {
  margin-top: 3rem;
}
.profile-wrapper button {
  position: absolute;
  right: 0;
  top: 0;
  background: none;
  color: blue;
  padding: 0;
}
.profile-wrapper:not(:last-child) {
  padding-bottom: 1rem;
  border-bottom: 1px solid #E6E6E6;
}
.profile-wrapper .profile-img {
  width: 5rem;
  height: 5rem;
  margin-bottom: 1rem;
}
.profile-wrapper .profile-img, .profile-wrapper .profile-img img {
  border-radius: 50%;
}
.profile-wrapper .bio {
  margin-top: 1rem;
}

